import { prefix } from '../../globals/settings';
import { html, LitElement, property } from 'lit-element';
import { carbonElement as customElement } from '../../globals/decorators/carbon-element';
import styles from './item-padding-container.scss';

/**
 * Section Title.
 *
 * @element cds-item-padding-container
 */

@customElement(`${prefix}-item-padding-container`)
export class CDSItemPaddingContainer extends LitElement {
  @property({ type: String }) vertical = '';
  @property({ type: String }) horizontal = '';

  static styles = styles;
  render() {
    return html` <div
      class="item-padding-container__vertical--${this
        .vertical} item-padding-container__horizontal--${this.horizontal}">
      <slot></slot>
    </div>`;
  }
}
